<template>
  <div class="welcome">
  <transition name="slidein" enter-active-class="slide_left_right">
    <h1 v-if="showName">Requests Manager</h1>
  </transition>
    <p>
      <i>- Open Source HTTP requests manager -</i>
    </p>
    <div class="download">
      <small>Supporting:</small>
      <p>Windows | macOS | Linux</p>
      <p>
        <a
          style="color: #92f7e1"
          :href="github_release_html_url"
          target="_blank"
          >Download latest build from Github >
        </a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "welcome",
  data() {
    return {
      github_release_html_url: null,
      showName: false
    };
  },
  methods: {
    fetchGithubLatestRelease() {
      fetch(
        "https://api.github.com/repos/Tafeen/RequestsManager/releases/latest",
        {
          method: "GET",
          headers: {
            Accept: "application/vnd.github.v3+json",
          },
        }
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            console.error("Github api returned: " + response);
          }
        })
        .then((response) => {
          this.github_release_html_url = response.html_url;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.fetchGithubLatestRelease();
  },
  mounted(){
    this.showName = !this.showName;
  }
};
</script>

