
<template>
  <div class="workflow">
    <h2>Workflow</h2>
    <ul>
      <li
        v-for="(step, index) in steps"
        v-bind:key="step.name"
        v-bind:data-index="index"
      >
        <intersect @enter="sth_inter(index)" rootMargin="-5%">
          <div>
            <transition
              name="slidein"
              v-bind:css="false"
              v-on:before-enter="beforeEnter"
              v-on:enter="enter"
            >
              {{ shownSteps[index] }}
              <div
                v-show="showSteps[index]"
                :class="{
                  step_left: index % 2 === 0,
                  step_right: index % 2 !== 0,
                }"
              >
                <div v-if="step.img">
                  <img :src="step.img" :alt="step.name" />
                </div>
                <div class="step_description">
                  <h3 v-html="step.name"></h3>
                  <p v-html="step.description"></p>
                </div>
              </div>
            </transition>
          </div>
        </intersect>
      </li>
    </ul>
  </div>
</template>
<script>
import Intersect from "vue-intersect";
import Velocity from "../../node_modules/velocity-animate/velocity";

export default {
  name: "workflow",
  components: { Intersect },
  data() {
    return {
      shownSteps: 0,
      showSteps: [false, false, false, false],
      steps: [
        {
          name: "Create workspace",
          description:
            "Start with new workspace. Create it while starting project to easily manage all your requests.",
          img: require("@/assets/img/create_workspace.png"),
        },
        {
          name: "Add requests",
          description:
            "Add requests - save them, test them and modify them as many times as you need!",
          img: require("@/assets/img/create_request.png"),
        },
        {
          name: "Connect with wiki provider",
          description:
            "Since now you will need just to switch tab to 'Wiki', rather than opening browser to find how to properly change this request",
          img: require("@/assets/img/connect_provider.png"),
        },
        {
          name: "Add colaborators <small>(Version 0.5)</small>",
          description:
            "We are planning to create shared workspaces - for <b>free and open sourced</b>, just as RequestsManager.",
        },
      ],
    };
  },
  methods: {
    sth_inter: function (index) {
      var self = this;
      if (index + 1 > self.shownSteps) {
        self.shownSteps += 1;
        self.showSteps[index] = true;
      }
    },
    beforeEnter: function (el) {
      el.style.opacity = 0;
    },
    enter: function (el, done) {
      var self = this;
        if (self.shownSteps % 2 == 0) {
          Velocity(
            el,
            { opacity: 1, translateX: "-2vw" },
            { complete: done }
          );
        } else {
          Velocity(el, { opacity: 1, translateX: "2vw" }, { complete: done });
        }
    },
  },
};
</script>

